import React, { useEffect, useRef, useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import './index.css';
import Profil from './images/profile_bg.png';
import { skills } from './types/mock-skills';
import { GrAchievement } from 'react-icons/gr';
import { GoGoal } from 'react-icons/go';

const Main = () => {
    const [typedText, setTypedText] = useState('');
    const [typingComplete, setTypingComplete] = useState(false);
    const sections = useRef<(HTMLElement | null)[]>([]);

    const fullText = 'Junior Fejleszt';
    const typingSpeed = 150;

    useEffect(() => {
        let i = 0;
        const typingInterval = setInterval(() => {
            if (i < fullText.length) {
                setTypedText(fullText.substring(0, i + 1));
                i++;
            } else {
                clearInterval(typingInterval);
                setTypingComplete(true);
            }
        }, typingSpeed);

        const observer = new IntersectionObserver(
            (entries) => {
                entries.forEach((entry) => {
                    if (entry.isIntersecting) {
                        entry.target.classList.add('animate-fade-in-up');
                    }
                });
            },
            { threshold: 0.1 },
        );

        sections.current.forEach((section) => {
            if (section) observer.observe(section);
        });

        return () => {
            clearInterval(typingInterval);
            sections.current.forEach((section) => {
                if (section) observer.unobserve(section);
            });
        };
    }, []);

    const scrollToSkills = () => {
        const skillsSection = document.getElementById('skills');
        if (skillsSection) {
            skillsSection.scrollIntoView({ behavior: 'smooth' });
        }
    };

    return (
        <div>
            {/* About Me Section */}
            <section
                id="intro"
                className="py-5 d-flex justify-content-start align-items-center mt-5"
                style={{ minHeight: '900px' }}
                ref={(el) => (sections.current[0] = el)}
            >
                <div className="container d-flex">
                    {/* Content on the left */}
                    <div className="col-md-6 text-start">
                        <h2 className="display-3">Rlam</h2>
                        <h3 className="display-4 text-muted">
                            {typedText}
                            {!typingComplete && <span className="typing-cursor"></span>}
                        </h3>
                        <p className="lead delay-1">
                            dvzlet! <strong>Boros Balzs Lszl</strong> vagyok,{' '}
                            <strong>szoftverfejleszt</strong>.<br />
                            <strong>Frontend fejleszts</strong> s <strong>UI/UX tervezs</strong>{' '}
                            terletn dolgozom, ahol a modern webtechnolgik kreatv alkalmazsa s
                            felhasznlkzpont megoldsok kialaktsa a f fkuszom.
                            <br />
                            <br />
                            Programozsi karrierem 6 ve kezddtt kzpiskolai tanulmnyaim sorn,
                            mely sorn szles kr technolgiai ismeretekre tettem szert. A
                            ksbbiekben ezeket a tapasztalatokat szeretnm tovbb mlyteni
                            egyetemi kpzs keretben.
                            <br />
                            <br />
                            Szakmai gyakorlatomat klnfle programozsi nyelvek s keretrendszerek
                            elsajttsa, valamint komplex projektek megvalstsa jellemzi.
                            Technolgiai stackem rszleteirt kattintson ide.
                        </p>
                        <button
                            onClick={scrollToSkills}
                            className="btn btn-primary btn-lg mt-3 delay-2"
                            style={{
                                borderRadius: '30px',
                                padding: '10px 30px',
                                fontSize: '18px',
                                transition: 'all 0.3s ease',
                            }}
                        >
                            Ismereteim
                        </button>
                    </div>

                    {/* Profile Picture on the right */}
                    <div className="col-md-6 d-flex flex-column align-items-center delay-3">
                        <img
                            src={Profil}
                            alt="Profile"
                            className="img-fluid"
                            style={{
                                maxHeight: '600px',
                                objectFit: 'cover',
                                borderRadius: '30px',
                                padding: '5px',
                                marginBottom: '15px',
                            }}
                        />
                        <div className="text-center" style={{ width: '100%' }}>
                            <span className="badge bg-primary p-3 current-work-badge">
                                Jelenleg sajt tleteket valstok meg
                            </span>
                        </div>
                    </div>
                </div>
            </section>

            {/* Skills Section */}
            <section
                id="skills"
                className="bg-light py-5 d-flex justify-content-center align-items-center"
                style={{ minHeight: '900px' }}
                ref={(el) => (sections.current[1] = el)}
            >
                <div className="container text-center">
                    <h2 className="display-3 mb-4">Ismereteim / TechStack</h2>
                    <div className="row mt-4 justify-content-center">
                        {skills.map((skill, index) => (
                            <div
                                key={index}
                                className="col-md-2 mt-4"
                                style={{ animationDelay: `${0.3 + index * 0.1}s` }}
                            >
                                <div className="card skill-card" style={{ width: '10rem' }}>
                                    <div className="card-body text-center">
                                        <h5 className="card-title">{skill.name}</h5>
                                        <img
                                            src={skill.imageURL}
                                            alt={skill.name}
                                            className="card-img-top"
                                            style={{ maxWidth: '50px', height: 'auto' }}
                                        />
                                    </div>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
            </section>

            {/* Career Section */}
            <section
                id="career"
                className="py-5 d-flex justify-content-center align-items-center"
                style={{ minHeight: '900px' }}
                ref={(el) => (sections.current[2] = el)}
            >
                <div className="container">
                    <h2 className="display-2 text-center mb-5">Mrfldkvek</h2>
                    <div className="row">
                        {/* Left Column - Achievements */}
                        <div className="col-md-6 mt-5 delay-1">
                            <h4 className="display-4 mb-3">
                                <GrAchievement /> Eredmnyek
                            </h4>
                            <ul className="list-unstyled">
                                <li className="current-work-badge">
                                    <h5>GBD Logistics Projekt</h5>
                                    <p className="text-muted">
                                        A <strong>GBD Logistics</strong> projekt egy logisztikai
                                        webes, asztali s mobil alkalmazs, amely a logisztikai
                                        cgek s vllalkozsok hatkonysgt nveli.
                                        <br />
                                        Egy <strong>hromfs csapat</strong> tagjaknt a mobil- s
                                        webalkalmazs fejlesztsrt feleltem, modern technolgik,
                                        mint a React (TypeScript), CSS, Bootstrap s Node.js
                                        alkalmazsval.
                                    </p>
                                </li>
                                <li>
                                    <h5>C1 Komplex Nyelvvizsga</h5>
                                    <p className="text-muted">
                                        Az angol nyelv irnti elktelezettsgem s szenvedlyem arra
                                        sztnztt, hogy magas szint nyelvtudsomat hivatalosan is
                                        igazoljam.
                                        <strong>A C1 komplex nyelvvizsga</strong> megszerzse
                                        nemcsak a nyelvi kszsgeim fejlesztst tette lehetv,
                                        hanem a <strong>szakmai</strong> s{' '}
                                        <strong>nemzetkzi</strong> kommunikciban val
                                        magabiztossgomat is megerstette. Ez a mrfldk fontos
                                        lps volt az egyetemi tanulmnyok irnyba.
                                    </p>
                                </li>
                            </ul>
                        </div>

                        {/* Right Column - Goals */}
                        <div className="col-md-6 mt-5 delay-2">
                            <h4 className="display-4 mb-3">
                                <GoGoal /> Clok
                            </h4>
                            <ul className="list-unstyled">
                                <li>
                                    <h5>Full-Stack fejleszts</h5>
                                    <p className="text-muted">
                                        Clom, hogy elmlytsem tudsomat a full-stack fejleszts
                                        terletn, belertve a backend technolgik,{' '}
                                        <strong>adatbzis-kezels</strong> s{' '}
                                        <strong>szerveroldali architektrk</strong> elsajttst.
                                        Ezltal kpes leszek{' '}
                                        <strong>komplex, end-to-end megoldsok</strong>{' '}
                                        ltrehozsra, amelyek a modern webes alkalmazsok teljes
                                        spektrumt lefedik.
                                    </p>
                                </li>
                                <li>
                                    <h5>UI/UX designols</h5>
                                    <p className="text-muted">
                                        Tbb idt szeretnk sznni a modern{' '}
                                        <strong>UI/UX trendek</strong> s tervezsi elvek
                                        tanulmnyozsra, hogy mg letisztultabb,{' '}
                                        <strong>felhasznlbartabb s eszttikusabb</strong>{' '}
                                        designokat kszthessek. Clom, hogy a felhasznli lmnyt
                                        a lehet legmagasabb szintre emeljem, mikzben a
                                        funkcionalitst is szem eltt tartom.
                                    </p>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </section>

            {/* Contact Section */}
            <section
                id="contact"
                className="bg-light py-5 d-flex justify-content-center align-items-center"
                style={{ minHeight: '900px' }}
                ref={(el) => (sections.current[3] = el)}
            >
                <div className="container text-center">
                    <h2 className="display-3 mb-4">Kapcsolat</h2>
                    <p className="lead delay-1" style={{ fontSize: '1.25rem' }}>
                        Ha szeretnl kapcsolatba lpni vagy krdsed van, keress bizalommal!
                    </p>
                    <form className="delay-2">
                        <div className="mb-3">
                            <label
                                htmlFor="name"
                                className="form-label"
                                style={{ fontSize: '1.1rem' }}
                            >
                                Neved
                            </label>
                            <input
                                type="text"
                                className="form-control"
                                id="name"
                                placeholder="Neved"
                                style={{ maxWidth: '500px', margin: '0 auto', fontSize: '1.1rem' }}
                            />
                        </div>
                        <div className="mb-3">
                            <label
                                htmlFor="email"
                                className="form-label"
                                style={{ fontSize: '1.1rem' }}
                            >
                                Email cmed
                            </label>
                            <input
                                type="email"
                                className="form-control"
                                id="email"
                                placeholder="Email cmed"
                                style={{ maxWidth: '500px', margin: '0 auto', fontSize: '1.1rem' }}
                            />
                        </div>
                        <div className="mb-3">
                            <label
                                htmlFor="message"
                                className="form-label"
                                style={{ fontSize: '1.1rem' }}
                            >
                                zeneted
                            </label>
                            <textarea
                                className="form-control"
                                id="message"
                                rows={4}
                                placeholder="rd ide zeneted..."
                                style={{ maxWidth: '500px', margin: '0 auto', fontSize: '1.1rem' }}
                            ></textarea>
                        </div>
                        <button
                            type="submit"
                            className="btn btn-primary"
                            style={{
                                fontSize: '1.2rem',
                                padding: '10px 20px',
                                borderRadius: '30px',
                                marginTop: '20px',
                            }}
                        >
                            zenet kldse
                        </button>
                    </form>
                </div>
            </section>

            {/* Footer Section */}
            <footer
                className="bg-dark text-white text-center py-3"
                ref={(el) => (sections.current[4] = el)}
            >
                <p>&copy; 2025 Boros Balzs, Minden jog fenntartva.</p>
            </footer>
        </div>
    );
};

export default Main;
